.profile{color:#153257}.profile--default-offset{padding-top:65px}@media (min-width:480px){.profile--default-offset{padding-top:calc(65px + (3000vw - 14400px)/920)}}@media (min-width:1400px){.profile--default-offset{padding-top:95px}}.profile__title{font-family:HelveticaNeueLTPro-Md,sans-serif;font-weight:400;font-size:40px;line-height:48px}.profile__content{max-width:710px}.profile__content h1,.profile__content h2,.profile__content h3,.profile__content h4,.profile__content h5,.profile__content h6{font-family:HelveticaNeueLTPro-Md,sans-serif;font-weight:400}.profile__content h2,.profile__content h3{font-size:40px;line-height:48px;margin-bottom:20px}.profile__content h4{font-size:16px;line-height:24px;margin-bottom:5px;margin-top:10px}.profile__content p{font-family:HelveticaNeueLTPro-Lt,sans-serif;font-size:16px;line-height:24px;margin-bottom:10px}.profile__content b{font-family:HelveticaNeueLTPro-Bd,sans-serif;font-size:20px;line-height:28px;margin-bottom:10px;display:inline-block}