.video-play{width:86px;height:86px;border-radius:50%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;background:#fff;-webkit-box-shadow:0 0 0 10px hsla(0,0%,100%,.6);box-shadow:0 0 0 10px hsla(0,0%,100%,.6)}@media (max-width:767px){.video-play{width:60px;height:60px;-webkit-box-shadow:0 0 0 5px hsla(0,0%,100%,.6);box-shadow:0 0 0 5px hsla(0,0%,100%,.6)}}.video-play--position-center{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.video-play:before{content:"";width:0;height:0;border-top:7px solid rgba(0,0,0,0);border-left:11px solid #4637a5;border-bottom:7px solid rgba(0,0,0,0)}.videos--default-offset{margin-bottom:50px}@media (min-width:480px){.videos--default-offset{margin-bottom:calc(50px + (8000vw - 38400px)/920)}}@media (min-width:1400px){.videos--default-offset{margin-bottom:130px}}.videos__item-video{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;border-radius:22px;background:#f4f4f7;max-width:680px;width:100%;min-height:460px;padding:10px;overflow:hidden}@media (max-width:1199px){.videos__item-video{min-height:auto;height:23.95vw}}@media (max-width:991px){.videos__item-video{min-height:280px;height:38.34vw;margin-top:15px;margin-bottom:15px}}.videos__item-video-image{position:absolute;left:10px;top:10px;width:calc(100% - 20px);height:calc(100% - 20px);-o-object-fit:cover;object-fit:cover;border-radius:22px;overflow:hidden}