.topics-title{margin-bottom:50px}.topics-item{margin-bottom:30px}@media (min-width:480px){.topics-item{margin-bottom:calc(30px + (6000vw - 28800px)/920)}}@media (min-width:1400px){.topics-item{margin-bottom:90px}}.topics-item:nth-child(odd){-webkit-box-orient:horizontal;-webkit-box-direction:reverse;-ms-flex-direction:row-reverse;flex-direction:row-reverse}.topics-item__content{max-width:680px}.topics-item__content-center{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:100%}.topics-item__image{position:relative;max-width:680px;max-height:430px;width:100%;height:22.4vw;min-height:280px;border-radius:16px;overflow:hidden}@media (max-width:991px){.topics-item__image{width:100%;height:35.31vw;min-height:300px;margin-bottom:30px}}.topics-item__image img{position:absolute;top:0;left:0;width:100%;height:100%;-o-object-fit:cover;object-fit:cover}.topics-item__content-title{font-family:HelveticaNeueLTPro-Bd,sans-serif;color:#153257;margin-bottom:20px;font-size:24px}@media (min-width:480px){.topics-item__content-title{font-size:calc(24px + (2000vw - 9600px)/920)}}@media (min-width:1400px){.topics-item__content-title{font-size:44px}}.topics-item__content-subtitle{font-family:HelveticaNeueLTPro-Md,sans-serif;font-size:20px;line-height:28px;-webkit-text-decoration-line:underline;text-decoration-line:underline;color:#153257;margin-bottom:14px}.topics-item__content-content{padding-bottom:30px;border-bottom:1px solid #daddf1}.topics-item__content-content p{font-family:HelveticaNeueLTPro-Lt,sans-serif;font-size:16px;line-height:24px;color:#153257}.topics-item__content-buttons{padding-top:28px}.topics-item__content-buttons a:not(:last-child){margin-right:40px}@media (max-width:600px){.topics-item__content-buttons a:not(:last-child){margin-right:20px;margin-bottom:14px}}