.accordion{color:#153257}.accordion__item{border-top:1px solid #c9e8da;padding-bottom:17px}.accordion__item:last-child{border-bottom:1px solid #c9e8da}.accordion__item:first-child .accordion__description{display:block}.accordion__item--active .accordion__title-icon svg{fill:#4637a5;-webkit-transform:scale(-1);-ms-transform:scale(-1);transform:scale(-1)}.accordion__title{position:relative;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;font-family:HelveticaNeueLTPro-Md,sans-serif;font-size:20px;line-height:28px;color:#153257;padding:23px 64px 6px 0;cursor:pointer}.accordion__title,.accordion__title-icon{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.accordion__title-icon{position:absolute;top:34px;right:40px;width:24px;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}@media (max-width:991px){.accordion__title-icon{right:15px}}.accordion__title-icon svg{-webkit-transition:all .15s ease-out;-o-transition:all .15s ease-out;transition:all .15s ease-out;fill:#a1adbc}.accordion__description{font-family:HelveticaNeueLTPro-Lt,sans-serif;font-size:16px;line-height:24px;display:none;color:#153257}.accordion__description p{max-width:680px}@media (max-width:1199px){.accordion__description p{width:85%}}